.background{
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;

}
.quote-box {
    border-radius: 3px;
    position: relative;
    margin:8% auto auto auto;
    width: 450px;
    padding: 40px 50px;
    display: table;
    background-color: #fff;
}
.phraseBox{
    text-align: center;
    width: 450px;
    font-weight: 500;
    font-size: 1.75em;
    line-height: normal;
}
#authorBox{
    width: 450px;
    height: auto;
    clear: both;
    padding-top: 20px;
    font-size: 1em;
    text-align: right;
      
}
.buttonsBox{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 20px;
}
#tweet-quote{
    
      
      font-size: 3em;
       opacity: 1;
      cursor: pointer;
}